// Grid settings
$main-sm-columns:       22;
$main-sm-columns-single:       22;
$sidebar-sm-columns:    4;
$main-sidebar-sm-columns:    2;
$main-sidebar-sm-columns-single:    1;

$grid-columns:	        24;
$grid-gutter-width:   28px;

$font-family-base: 'Encore regular';
$font-family-light: 'Encore light';

$black: #231f20;
$ritch_black: #000;
$white: #fff;


$navbar-padding-y:    1.8rem;
$navbar-padding-x:    0rem;

$carousel-indicator-active-bg:       $black !default;

$carousel-indicator-width: 15px;
$carousel-indicator-height: 15px;

$carousel-control-width: .65rem;
$carousel-control-arrow-width: 1.5rem;

$list-group-item-padding-x: .25rem;

$list-group-border-width: 0;

$navbar-nav-link-padding-x:  1rem;
$navbar-nav-link-padding-y: 1.5rem;

$border-light-color:  #e7e7e7;

/**
 * WooCommerce CSS Variables
 */

$woocommerce:   	#a46497 !default;
$green:         	#7ad03a !default;
$red:           	#a00 !default;
$orange:        	#ffba00 !default;
$blue:          	#2ea2cc !default;

$primary:           #231f20 !default;                                    // Primary color for buttons (alt)
$primarytext:       desaturate(lighten($primary, 50%), 18%) !default;    // Text on primary color bg

$secondary:         desaturate(lighten($primary, 40%), 21%) !default;    // Secondary buttons
$secondarytext:     desaturate(darken($secondary, 60%), 21%) !default;   // Text on secondary color bg

$highlight:         adjust-hue($primary, 150deg) !default;               // Prices, In stock labels, sales flash
$highlightext:      desaturate(lighten($highlight, 50%), 18%) !default;  // Text on highlight color bg

$contentbg:         #fff !default;                                       // Content BG - Tabs (active state)
$subtext:           #767676 !default;                                    // small, breadcrumbs etc
