.test {
  border: 1px solid red;
}
html, body {
  height: 100%;
}

.page-template-template-split {
    & section {
      background-color: transparent;
      height: 100%;
      width: 100%;
      position: fixed;
    }
    & .menu {
      z-index: 2000;
      padding: 0 0 0 1rem;
    }

    & .navbar-right {
      height: 100vh;
      right: 0;
      position: absolute;
      z-index: 2000;
    }
}

.navbar-right {
  height: 100vh;
  right: 0;
  position: absolute;
  z-index: 2000;
  //width: 100%;
}

.left, .right {
  z-index: 1;
  transition: transform .5s;
}
.active {
  z-index: 1;
  .left, .right {
    transform: translateY(0);
    transition: transform .5s;
  }
}
.left {
  height: 100%;
  //width: 50%;
  position: absolute;
  transform: translateY(-100%);
  & img {
  //  height: 100%;
  }

}
.right {
  height: 100%;
  //width: 50%;
  position: absolute;
  right: 2rem;
  transform: translateY(100%);
  & .thumb {
    bottom: 2rem;
    position: absolute;
  }

}

.carousel-indicators li {
  border-radius: 100%!important;
}


.grid-item {
  position: relative;
  text-align: center;
  color: white;
  width: 360px;
height: auto;
  & a {
      color: $white;
      & .title {
        opacity: 0;
      }
    & h3 {
      color: $white;
      opacity: 0;
    }
  }
  &:hover a {
    & .title {
      opacity: 1;
    }
    & h3 {
      color: $white;
      opacity: 1;
    }
  }
    & .title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    //text-align: left;
    //background-color: rgba(255, 255, 255, 0.8);
    & h3 {
      //font-size: .9rem;
    }
  }
  & img {
    //filter: brightness(10%);
    filter: grayscale(100%);
    opacity: .85;
    transition: .5s;
    &:hover {
      filter: grayscale(0%);
      opacity: 1;
    }
  }
}
