.jumbotron {
  position: relative;
  overflow: hidden;
  background-color:black;
  &.video {
    height: 70vh;
  }
  & a {
    color: $white;
  }
  & .list-group-item {
  position: relative;
  display: block;
  padding: 0 $list-group-item-padding-x;
  color: $list-group-color;
  text-decoration: if($link-decoration == none, null, none);
  background-color: transparent;
  border: $list-group-border-width solid $list-group-border-color;
  font-size: 1rem;
}
}
.jumbotron video {
  position: absolute;
  z-index: 1;
  top: 0;
  width:100%;
  height:100%;
  /*  object-fit is not supported on IE  */
  object-fit: cover;
  opacity:1;
}
.jumbotron .container {
  z-index: 2;
  position: relative;
}
