.noUi-target,
.noUi-target * {
    touch-action: none;
    user-select: none;
    box-sizing: border-box;
}
.noUi-base,
.noUi-connects {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
}
.noUi-connect,
.noUi-origin {
    will-change: transform;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    transform-origin: 0 0;
    transform-style: flat;
}
html:not([dir="rtl"]) .noUi-horizontal .noUi-origin {
    left: auto;
    right: 0;
}
.noUi-horizontal .noUi-origin {
    height: 0;
    width: 10%;
}
.noUi-touch-area {
    height: 100%;
    width: 100%;
    padding: .35rem 0;
    & input {
      border: 0;
      color: $white;
      font-family: $font-family-light;
      font-size: .875rem;
      text-align: center;
      width: 2.828rem;
      height: 2.576rem;
      left: -0.8125rem;
      top: -2.375rem;
      position: relative;
      background: no-repeat;
      background-position: left;
      background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' fill='#{$primary}' width='45.25' height='41.23' viewBox='0 0 45.25 41.23'><path d='M40.223,0.001H5.027C2.25,0.001,0,2.252,0,5.029v22.625c0,2.777,2.251,5.028,5.028,5.028h7.542l10.056,8.547l10.056-8.547
    		h7.542c2.777,0,5.028-2.251,5.028-5.028V5.029C45.25,2.252,43,0.001,40.223,0.001'/></svg>") ;
    }
}
.noUi-state-tap .noUi-connect,
.noUi-state-tap .noUi-origin {
    transition: transform 0.3s;
}
.noUi-state-drag * {
    cursor: inherit !important;
}
.noUi-horizontal {
    height: 4px;
}
.noUi-horizontal .noUi-handle {
    width: 21px;
    height: 21px;
    left: -10px;
    top: -8px;
    text-align: center;
    background: $white;
    color: $white;
    border-radius: 1rem;
    @extend .shadow-sm;
}
html:not([dir="rtl"]) .noUi-horizontal .noUi-handle {
    right: -1.15rem;
    left: auto;
}
.noUi-target {
    position: relative;
    direction: ltr;
    background: $border-light-color;;
    border-radius: 4px;
    //border: 1px solid #D3D3D3;
    padding: 0 8px;
}
.noUi-connects {
    border-radius: 3px;
    overflow: hidden;
    z-index: 0;
}
.noUi-connect {
    height: 100%;
    width: 100%;
    background: $primary;
}
.noUi-draggable {
    cursor: ew-resize;
}
.noUi-handle {
    position: absolute;
    border: 1px solid #D9D9D9;
    border-radius: 3px;
    background: #FFF;
    cursor: default;
}
