@font-face {
    font-family: 'Encore light';
    src: url('../fonts/PFEncoreSansPro-Light.eot');
    src: url('../fonts/PFEncoreSansPro-Light.eot?#iefix') format('embedded-opentype'),
        url('../fonts/PFEncoreSansPro-Light.woff2') format('woff2'),
        url('../fonts/PFEncoreSansPro-Light.woff') format('woff'),
        url('../fonts/PFEncoreSansPro-Light.svg#PFEncoreSansPro-Light') format('svg');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Encore regular';
