.woocommerce, .post-type-archive-portfolio, .woocommerce-checkout, .woocommerce-account {
  & main {
    padding-top: 6rem;
  }
}


.current_page_parent {
  & .nav-link {
    color: $primary!important;
  }
}

article {
  padding: 5rem 0 0 0;
}

/* clear fix */
ul.gallery {
  & .list-group-item {
    padding: .25rem .25rem;
}
}
/* clear fix */
ul.gallery:after {
  content: '';
  display: block;
  clear: both;
}



.gallery-item--width2 { width: 200px; }
.gallery-item--height2 { height: 200px; }
