// Grid system
.main {
  @include make-col-ready();
  //  @include make-col-offset(4);
  @include media-breakpoint-up(sm) {
    padding: 0;
    @include make-col($main-sm-columns);
    .sidebar-primary & {
      @include make-col($main-sm-columns - $sidebar-sm-columns);
    }
  }
}
.sidebar {
  padding-top: 7rem;
  @include make-col-ready();
  @include media-breakpoint-up(sm) {
    @include make-col($sidebar-sm-columns);
  }
}
.main-sidebar {
  @include make-col-ready();
  @include media-breakpoint-up(sm) {
    @include make-col($main-sidebar-sm-columns-single);
  }
}
.page-template-template-split {
    & .main-sidebar {
      @include make-col-ready();
      @include media-breakpoint-up(sm) {
        @include make-col($main-sidebar-sm-columns);
      }
    }
    & .main {
      @include make-col-ready();
      @include make-col-offset(0);
      @include media-breakpoint-up(sm) {
        padding: 0;
        @include make-col($main-sm-columns-single);

      }
    }
}

.single-portfolio {
    & .main {
      @include make-col-ready();
      @include make-col-offset(0);
      @include media-breakpoint-up(sm) {
        padding: 0;
        @include make-col($main-sm-columns);

      }
    }
}

.woocommerce-cart {
    & .main {
      @include make-col-ready();
      @include make-col-offset(4);
      @include media-breakpoint-up(sm) {
        padding: 0;
        @include make-col(20);
      }
    }
    & .woocommerce {
      @extend .container;
    }
}

.entry-content {
  // /@extend .col-20;
  @include make-col(17);
  @include make-col-offset(5);
}

aside {
  @extend .px-0;
  &.sidebar {
    @include make-col(5);
    //position: fixed;
    //left: 0;
  }
  &.main-sidebar {
    position: fixed;
    right: 0;
    z-index: 2000;
  }
}

.woocommerce-form-coupon-toggle {
  @include make-col(14);
}
