section {
  &.widget {
    padding: 0 2rem 2rem 2rem;
    & .facetwp-facet-price {
      padding: 2rem 0 0 0;
    }
    & .separator {
    font-family: 'Encore ExtraThin Italic';
    display: flex;
    align-items: center;
    text-align: center;
    padding: 0 0 1rem 0;
    letter-spacing: .08rem;
    }
    & .separator::before, .separator::after {
        content: '';
        flex: 1;
        border-bottom: 1px dotted $primary;
    }
    & .separator::before {
        margin-right: .25em;
    }
    & .separator::after {
        margin-left: .25em;
    }
  }
}

